@import './reset.scss';

$mainColor : #ff0000;

.title{
    color:$mainColor;
}
// div.container p {
//     color: blue;
// }
div.container{
    h4{
        color: green;
    }
}

.alert-yellow{
    @extend .alert;
    background-color: yellow;
}
@mixin alert(){
    background-color: #eee;
    border-radius: 5px;
    color: tomato;
    font-weight: bold;
    height: 40px;
    line-height: 40px;
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
}
.alert{
    @include alert()
}